import { createRouter, createWebHistory } from "vue-router";
import Home from "../views/Home.vue";
import Tech from "../views/Tech.vue";
import Business from "../views/Business.vue";
import Sports from "../views/Sports.vue";
import News from "../views/News.vue";
import BusinessFirstPost from "../views/BusinessFirstPost.vue";


const routes = [
  {
    path: "/",
    name: "Home",
    component: Home,
  },
  {
    path: "/business",
    name: "Business",
    component: Business
    
  },
  {
    path: "/tech",
    name: "tech",
    component: Tech
  },
  {
    path: "/sports",
    name: "Sports",
    component: Sports
  },
  {
    path: "/news",
    name: "News",
    component: News
  },
  {
    path: "/business-first",
    name: "BusinessFirstPost",
    component: BusinessFirstPost
  }
];

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes,
});

export default router;
